import React from 'react'
import ReactMarkdown from 'react-markdown'
import summary from '../content/output/ilab-summary.json'

const names = [
  'shivesh-jadon', 
  'neil-chulpongsatorn',
  'marcus-friedel',
  'adnan-karim',
  'aditya-gunturu'
]

const types = [
  {key: 'faculty', title: 'Faculty'},
  {key: 'postdoc', title: 'Post Docs'},
  {key: 'phd', title: 'PhD Students'},
  {key: 'master', title: 'Masters Students'},
  {key: 'undergrad', title: 'Undergrad Students'},
  {key: 'visiting', title: 'Visiting Researchers'},
  {key: 'alumni', title: 'Alumni'}
]

let students = []
for (let name of names) {
  let student = summary.fileMap[`content/output/people/${name}.json`]
  student.photo = `https://raw.githubusercontent.com/ucalgary-ilab/ilab-website/master/static/images/people/${name}.jpg`
  students.push(student)
}


class Students extends React.Component {
  componentDidMount() {
  }

  renderLink(student, key) {
    if (!student[key]) {
      return ''
    }

    let title = student[key].split('/')[3]
    let href
    let icon
    switch(key) {
      case 'url':
        icon = 'fas fa-link fa-fw'
        break
      case 'scholar': 
        icon = 'fas fa-graduation-cap fa-fw'
        break
      case 'twitter':
        icon = 'fab fa-twitter fa-fw'
        break
      case 'facebook':
        icon = 'fab fa-facebook-square fa-fw'
        break
      case 'github':
        icon = 'fab fa-github-alt fa-fw'
        break
      case 'cv':
        icon = 'far fa-file fa-fw'
        break
      case 'email':
        icon = 'far fa-envelope fa-fw'
        break
      case 'linkedin':
        icon = 'fab fa-linkedin-in fa-fw'
        break
    }

    return (
      <div className="item">
        <a href={ student[key] } target="_blank" style={{ fontSize: '1.2em' }}>
          <i className={ icon } />
        </a>
      </div>
    )
  }  

  getTitle(student) {
    switch (student.type) {
      case 'postdoc':
        student.title = 'Postdocotral Fellow'
        break
      case 'phd':
        student.title = 'PhD Student'
        break
      case 'master':
        student.title = 'Master Student'
        break
      case 'undergrad':
        student.title = 'Undergraduate Student'
        break
      case 'visiting':
        student.title = 'Visiting Researcher'
        break
      case 'alumni':
        switch (student.past) {
          case 'postdoc':
            student.title = 'Alumni (PostDoc)'
            break
          case 'phd':
            student.title = 'Alumni (PhD)'
            break
          case 'master':
            student.title = 'Alumni (Master)'
            break
          case 'undergrad':
            student.title = 'Alumni (Undergrad)'
            break
          case 'visiting':
            student.title = 'Alumni (Visiting)'
            break
        }
        break
    }
    return student
  }


  render() {
    return (
      <div id="students" >
        <h1>Students</h1>
        <div className="ui cards">
          { students.map((student) => {
            student = this.getTitle(student)
            return (
              <div className="card">
                <div className="center aligned content">
                  <div className="header">
                    <img className="ui small avatar image" src={ student.photo } />
                    { student.name }
                  </div>
                  <div className="meta">
                    <p className="ui label">
                      { student.title }
                    </p>
                  </div>
                  <div className="description">
                    <div className="ui horizontal small divided link list">
                      { ['cv', 'url', 'scholar', 'facebook', 'twitter', 'github', 'linkedin', 'email'].map((key) => {
                        return this.renderLink(student, key)
                      }) }
                    </div>
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}

export default Students
